.fromm .chatpage {
    position: relative;
    padding-top: 20px;
    width: 100%;
    height: 100%;
    background-color: var(--fromm-bg);
    overflow-y: scroll;
    overflow: overlay;
}

.fromm .chat-date {
    margin: 15px 0;
}

.fromm .chat-date.first-date {
    margin-top: 0;
}

.fromm .chat-date-text {
    padding: 8px 16px;
    border-radius: 30px;
    width: fit-content;
    background-color: var(--fromm-chat-date-bg);
    font-size: 12px;
    color: var(--fromm-chat-date-text);
}

.fromm .chat-piece {
    margin: 0 0 15px 15px;
}

.fromm .profile>img {
    width: 40px;
    aspect-ratio: 1/1;
    object-fit: cover;
    border-radius: 100px;
}

.fromm .chat-r {
    margin-left: 10px;
}

.fromm .artist-name {
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 12px;
    color: var(--fromm-chat-artist);
}

.fromm .bubble {
    border-radius: 2px 22px 22px 22px;
    padding: 13px 14px;
    background-color: var(--fromm-chat-bg);
    max-width: 220px;
    font-size: 14px;
    color: var(--fromm-chat-text);
    /* white-space: normal; */
    word-wrap: break-word;
}

.fromm .bubble.voice {
    border-radius: 22px;
    padding: 0;
    width: 200px;
    height: 32px;
}

.fromm .bubble.voice audio {
    border-radius: 22px;
    padding: 0 !;
    width: 100%;
    height: 100%;
    background-color: var(--fromm-chat-bg);
}

.fromm .bubble.voice audio::-webkit-media-controls-panel {
    background-color: var(--fromm-chat-bg);
}

.fromm .bubble.voice audio::-webkit-media-controls-panel {
    padding: 0;
}

.fromm .bubble.voice audio::-webkit-media-controls-current-time-display,
.fromm .bubble.voice audio::-webkit-media-controls-time-remaining-display {
    font-size: 10px !important;
    color: var(--fromm-chat-text);
}

.fromm .bubble.video {
    border-radius: 22px;
    padding: 0;
    width: 200px;
}

.fromm .bubble.video video {
    border-radius: 22px;
    border: none;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.fromm .bubble.image {
    border-radius: 22px;
    padding: 0;
    width: 200px;
    height: 200px;
}

.fromm .bubble.image img {
    border-radius: 22px;
    width: 200px;
    height: 200px;
    object-fit: cover;
}

.fromm .bubble .mention {
    font-weight: bold;
    color: skyblue;
}

.fromm .bubble-time {
    justify-content: flex-end;
    align-items: flex-start;
    margin: 0 5px;
    font-size: 12px;
    color: var(--fromm-chat-time);
}

@media (max-height: 620px) {
    .fromm .chat-date-text {
        font-size: 0.8em;
    }

    .fromm .profile>img {
        width: 30px;
    }

    .fromm .artist-name {
        font-size: 0.8em;
    }

    .fromm .bubble {
        border-radius: 2px 15px 15px 15px;
        padding: 9px 11px;
        max-width: 120px;
        font-size: 0.875em;
    }

    .fromm .bubble.video,
    .fromm .bubble.video video {
        border-radius: 15px;
        width: 100px;
    }

    .fromm .bubble.image,
    .fromm .bubble.image img {
        border-radius: 15px;
        width: 100px;
        height: 100px;
    }

    .fromm .bubble-time {
        font-size: 0.8em;
    }
}
