.fab-artists-page {
    gap: 20px;
    width: 100%;
    height: 100%;
}

.chat-list {
    height: 100%;
}

.chat-list>.chat-list-block {
    display: flex;
    justify-content: space-between;
    margin: 20px 20px 0px 20px;
    padding: 20px;
    background-color: var(--light-gray2);
}

.chat-list>.chat-list-block:hover {
    background-color: var(--light-gray-hover);
    cursor: pointer;
}

.fab .artist-list {
    flex-grow: 1;
    width: 100%;
}

.fab .artist-list-img {
    margin: 10px;
    border-radius: 100px;
    width: 40%;
    min-width: 90px;
    min-height: 90px;
    max-width: 180px;
    max-height: 180px;
    aspect-ratio: 1/1;
    cursor: pointer;
}

.fab .artist-list-img:hover {
    opacity: 0.8;
}

.fab .artist-search {
    padding: 20px 0;
    border-top: 1px solid #ccc;
    width: 85%;
}

.fab .artist-search h2 {
    margin-top: 0;
}

.fab .artist-search select {
    margin-bottom: 10px;
    padding: 5px 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 100%;
    font-size: 16px;
    cursor: pointer;
}

.fab .artist-search .search-text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.fab .artist-search input {
    flex-grow: 2;
    margin-right: 10px;
    padding: 5px 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
}

.fab .artist-search button {
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    width: auto;
    min-width: 50px;
    background-color: #ddd;
    font-size: 16px;
    cursor: pointer;
}

.fab .artist-search button:hover {
    background-color: #bbb;
}

/* for mobile */
@media (max-width: 425px) {}

@media (max-height: 620px) {
    .fab .artist-list {
        flex-direction: row;
        justify-content: center;
        align-items: space-between;
    }
}
