.vlive.chat-page {
    justify-content: space-between !important;
    height: 100%;
}

.vlive .chats {
    justify-content: flex-start;
    align-items: flex-start;
    padding: 15px 0;
    width: 100%;
    height: 100%;
    overflow: overlay;
    overflow-y: scroll;
}

.vlive .chat-bubble-blue {
    margin: 5px 20px;
    padding: 13px;
    width: auto;
    background-color: #86F3FC;
}

.vlive .chat-bubble-blue.chat-eng {
    background-color: rgb(140, 220, 255);
}

.vlive .chat-bubble-blue p {
    margin: 0;
    font-size: 14px;
}

.vlive .chat-bubble-blue .artist-name {
    margin-bottom: 5px;
    font-size: 12px;
    color: #40797D;
    font-weight: bold;
    user-select: none;
}

.vlive .chat-img-blue {
    margin: 5px 20px;
    width: 250px;
}

.vlive .chat-voice-blue {
    margin: 5px 20px;
    height: 42.5px;
    background-color: #86F3FC;
}

.vlive .chat-voice-blue audio {
    height: 42.5px;
}

audio::-webkit-media-controls-panel {
    background-color: #86F3FC;
}

@media (max-height: 620px) {
    .vlive .chats {
        padding: 10px 0;
    }

    .vlive .chat-bubble-blue {
        padding: 10px;
    }

    .vlive .chat-bubble-blue p {
        font-size: 0.9em;
    }

    .vlive .chat-bubble-blue .artist-name {
        font-size: 0.8em;
    }
}
