.fab .chat {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
}

.fab .chat {
    overflow-y: scroll;
    overflow: overlay;
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.chat>.block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    /* width: 380px; */
}

/* .chat>.block:first-child {
    margin-top: 50px;
} */

.chat>.block:last-child {
    margin-bottom: 20px;
}

.chat>.block:not(:first-child) {
    margin-top: 15px;
}

.chat>.block>.top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.chat>.block>.right {
    margin-left: 7px;
}

.chat>.block>.block-top {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.chat>.block>.block-top>.notice {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 45px;
    height: 45px;
    border-radius: 100px;
    background-color: #D5D5D9;
    color: #fff;
}

.chat>.block .message {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-left: 10px;
    max-width: 80%;
}

.chat>.block .time {
    margin: 5px 0 0 48px;
    font-size: 10px;
    color: #969696;
}

@media (max-height: 620px) {
    .chat>.block .time {
        font-size: 0.875em;
    }
}
