.profile>img {
    width: 45px;
    border-radius: 100px;
}

.profile>.star {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    margin-left: 30px;
    margin-top: 30px;
    width: 17px;
    height: 17px;
    padding-top: 1px;
    padding-left: 1px;
    background-color: #8457DD;
    font-size: 10px;
    color: white;
}

@media (max-height: 620px) {
    .profile>img {
        width: 38px;
    }
}
