.platform-logo {
    flex-grow: 1;
    width: 100%;
    height: 100%;
    cursor: pointer;
    overflow: hidden;
}

.platform-logo:hover {
    background-color: rgba(50, 50, 50, 0.1);
}

.platform-logo>div {
    height: 80%;
    max-height: 150px;
    max-width: 150px;
    aspect-ratio: 1/1;
}

.platform-logo img {
    width: 100%;
    aspect-ratio: 1/1;
    object-fit: cover;
}