.fab .search-results {
    flex-grow: 1;
    padding: 10px;
    width: 85%;
    height: 90%;
    user-select: none;
}

.search-block {
    height: 90%;
    overflow-y: scroll;
}

.search-block h5 {
    margin-top: 0;
    margin-bottom: 5px;
    color: #969696;
}

.search-bubble {
    margin-bottom: 15px;
    padding-top: 15px;
    padding-bottom: 5px;
    border-radius: 10px;
    background-color: rgba(150, 150, 150, 0.15);
    cursor: pointer;
}

.search-bubble:hover {
    background-color: rgba(150, 150, 150, .3);
}

.search-bubble .block {
    margin-bottom: 10px !important;
    width: 90%;
    padding-left: 20px;
}
