.mobile.msgpage {
    justify-content: space-between;
    align-items: center;
    background-color: white;
}

.mobile.msgpage .header {
    border-bottom: 1px solid #eee;
}

.mobile.msgpage .body {
    position: relative;
}

.msgpage .msg-body {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: center;
    padding: 0;
    width: 100%;
    /* height: 100%; */
    height: calc(100% - 60px);
    background-color: white;
    overflow-y: scroll;
    overflow: overlay;
}

.msg-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 10px;
    width: 100%;
    height: 100%;
    text-align: center;
}

.msg-text p {
    margin: 0;
    font-size: 12px;
    line-height: 1.5em;
    white-space: pre-wrap;
}

.msg-text p .msg-text-small {
    font-size: 10px;
}

.msg-text p .msg-text-big {
    font-size: 14px;
}

.msg-text p .msg-text-red {
    color: #ED4032;
}

.msg-text p .msg-text-blue {
    color: #3E8DEC;
}

.msg-text p .msg-text-green {
    color: #58BA36;
}

.msg-text p .msg-text-pink {
    color: #EA3BC9;
}

.msgpage .artist-msg {
    aspect-ratio: 0;
}

.msg-text .artist-msg img {
    object-fit: cover;
    border: none;
    cursor: pointer;
}

.msg-text .artist-msg img:not(:last-child) {
    margin-right: 5px;
}

.msg-text .artist-msg.msg-img-1 img { /* One image in a line */
    width: 305px;
    height: 305px;
}

.msg-text .artist-msg.msg-img-2 img { /* Two images in a line */
    width: 150px;
    height: 150px;
}

.msg-body .artist-msg.full-img {
    width: 100%;
    height: 100%;
    border: none;
    background-color: black;
}

.msg-body .artist-msg.full-img img {
    width: 100%;
    height: 100%;
    border: none;
}

.msg-body .artist-msg.video {
    width: 100%;
    height: 100%;
    border: none;
    background-color: black;
}

.msg-body .artist-msg video {
    width: 100%;
    height: 100%;
    border: none;
}

.msgpage .msg-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0 10px 0;
    border-top: 1px solid #eee;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    width: 100%;
    height: 60px;
    background-color: white;
}

.footer-left {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-left: 10px;
    width: 100%;
    height: 100%;
}

.msgpage .msg-footer .footer-left .artist-profile {
    margin-right: 10px;
    width: 50px;
    border-radius: 50px;
}

.msgpage .msg-footer .footer-left {
    font-size: 12px;
}

.msgpage .msg-footer .footer-left p {
    margin: 0;
}

.msgpage .msg-footer .footer-left .profile-name {
    margin-bottom: 5px;
    font-size: 14px;
    font-weight: bold;
}

.msgpage .msg-footer .footer-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    height: 100%;
}

.msgpage .msg-footer .footer-right>.footer-icon {
    margin: 0 10px 5px 10px;
    font-size: 18px;
    color: #000;
    cursor: pointer;
}

.msgpage .msg-footer .footer-right p {
    text-align: center;
    margin: 0;
    font-size: 12px;
}

.msg-img {
    z-index: 500;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .75);
}

.msg-img img {
    width: 350px;
    border: none;
    cursor: pointer;
    box-shadow: 0 0 20px rgba(0, 0, 0, .3);
}

.msg-img button {
    margin-top: 20px;
    padding: 10px;
    border: none;
    border-radius: 100px;
    width: 30px;
    height: 30px;
    background-color: rgba(255, 255, 255, 0.75);
    cursor: pointer;
}

.msg-img button:hover {
    background-color: rgba(255, 255, 255, 0.5);
}

@media (max-height: 620px) {
    .msg-text .artist-msg.msg-img-1 img { /* One image in a line */
        width: 200px;
        height: 200px;
    }

    .msg-text .artist-msg.msg-img-2 img { /* Two images in a line */
        width: 100px;
        height: 100px;
    }

    .msg-text p {
        font-size: 0.875em;
        line-height: 2.5em;
    }

    .msg-img img {
        width: 200px;
        border: none;
        cursor: pointer;
        box-shadow: 0 0 20px rgba(0, 0, 0, .3);
    }

    .msgpage .msg-footer .footer-left .profile-name {
        font-size: 1.1em;
    }
}
