:root {
  --fab-bg: #F5F4F5;
  --fromm-bg: #17181B;
  --fromm-subtext: #595C65;
  --fromm-chat-date-bg: #26282E;
  --fromm-chat-date-text: #7E7F88;
  --fromm-chat-bg: #37383E;
  --fromm-chat-text: #D3D4D9;
  --fromm-chat-artist: #84848F;
  --fromm-chat-time: #5B5C65;
  --fromm-voice-bg: #311F25;
  --fromm-voice-text: #E86277;
  /* Dark: 8457DD */
  /* Light: E1DDED */
  --light-gray1: #F5F4F5;
  --light-gray2: #F1F0F4;
  --light-gray2-hover: #CFCDD4;
}

* {
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h2 {
  font-size: 1.875em;
}

h3 {
  font-size: 1.705em;
}

h4 {
  font-size: 1.535em;
}

h5 {
  font-size: 1.365em;
}

p, div {
  font-size: 0.875em;
}

select, input, button {
  font-size: 0.875em !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.emoji-display {
  font-family: "Noto Color Emoji", "Apple Color Emoji", "Segoe UI Emoji", Times, Symbola, Aegyptus, Code2000, Code2001, Code2002, Musica, serif, LastResort !important;
}

.kaomoji-display {
  font-family: "Gill Sans" !important;
}

::-webkit-scrollbar {
  border-radius: 5px !important;
  background-color: rgba(238, 238, 238, 0.1) !important;
}

::-webkit-scrollbar-track {
  border-radius: 5px !important;
  background-color: rgba(220, 220, 220, 0.5) !important;
}

::-webkit-scrollbar-track-piece {
  border-radius: 5px !important;
  background-color: rgba(220, 220, 220, 0.5) !important;
}

::-webkit-scrollbar-thumb {
  border-radius: 50px !important;
  background-color: rgba(102, 102, 102, 0.9) !important;
}
